}

.card-open .card-header {
  color: #f6f8fa;
  background-color: #1447c8;
  border-bottom: 2px solid #1447c8;
  border-radius: 4px 4px 6px 6px;
}

.card-open .card-header .fa {
  color: #f6f8fa;
}

.ui-selectmenu-button {
  appearance: none;
  cursor: pointer;
  border: 2px solid #1447c8;
  border-radius: 5px;
  outline: none;
  width: max-content;
  max-width: calc(100vw - 3.5rem);
  padding: 1rem calc(1.125rem + 42px) 1rem 1.5rem;
  font-size: 1.25rem;
  font-weight: 600;
  transition: all .25s;
  display: inline-block;
}

.ui-selectmenu-button-closed {
  background: #fff url("select-arrow-blue.4649f979.svg") 100% no-repeat;
}

.ui-selectmenu-button-closed .ui-selectmenu-text {
