}

.ui-selectmenu-button-closed .ui-selectmenu-icon {
  background-image: url("select-arrow-blue.4649f979.svg");
}

.ui-selectmenu-button-closed:hover {
  background: #193787 url("select-arrow-white.0e993998.svg") 100% no-repeat;
}

.ui-selectmenu-button-closed:hover .ui-selectmenu-text {
  color: #fff;
}

.ui-selectmenu-button-closed:hover .ui-selectmenu-icon {
  background-image: url("select-arrow-white.0e993998.svg");
}

.ui-selectmenu-button-open {
  background: #193787 url("select-arrow-white.0e993998.svg") 100% no-repeat;
}

.ui-selectmenu-button-open .ui-selectmenu-text {
  color: #fff;
}

.ui-selectmenu-menu .ui-menu-item {
  display: none;
}

.ui-selectmenu-open {
  background-color: #fff;
  border: 2px solid #1447c8;
  border-radius: 5px;
  width: max-content;
}

.ui-selectmenu-open .ui-menu-item {
  cursor: pointer;
  padding: 1rem 1.5rem;
  font-size: 1.25rem;
  font-weight: 600;
  list-style-type: none;
  transition: all .25s;
  display: block;
}

.ui-selectmenu-open .ui-menu-item .ui-menu-item-wrapper {
  color: #1447c8;
}

.ui-selectmenu-open .ui-menu-item:hover {
  background-color: #193787;
}

.ui-selectmenu-open .ui-menu-item:hover .ui-menu-item-wrapper {
  color: #fff;
}

.team-info {
